import React, { Component, PropTypes } from "react";
import ReactDOM from "react-dom";
import GridCell from "dnn-grid-cell";
import Output from "./Output";
import Input from "./Input";
import { connect } from "react-redux";
import Localization from "localization";
import util from "../utils";
import "./Prompt.less";

class App extends Component {
    constructor() {
        super();
        this.keyDownHandler = this.onKeyDown.bind(this);
        this.clickHandler = this.onClickHandler.bind(this);
        this.mouseDownHandler = this.onMouseDownHandler.bind(this);
        this.mouseUpHandler = this.onMouseUpHandler.bind(this);
        this.isDragging = false;
    }
    componentDidMount() {
        document.addEventListener('keydown', self.keyDownHandler);
        document.addEventListener('keydown', self.keyDownHandler);
        document.addEventListener('mousedown', self.mouseDownHandler);
        document.addEventListener('mouseup', self.mouseUpHandler);
        document.addEventListener('click', self.clickHandler); this._isMounted = true;
        this._isMounted = true;
    }

    componentWillUnmount() {
        document.removeEventListener('keydown', self.keyDownHandler);
        document.removeEventListener('keydown', self.keyDownHandler);
        document.removeEventListener('mousedown', self.mouseDownHandler);
        document.removeEventListener('mouseup', self.mouseUpHandler);
        document.removeEventListener('click', self.clickHandler);
        this._isMounted = false;
    }

    handleClick(event) {
        // Note: this workaround is needed in IE. The remove event listener in the componentWillUnmount is called
        // before the handleClick handler is called, but in spite of that, the handleClick is executed. To avoid
        // the "findDOMNode was called on an unmounted component." error we need to check if the component is mounted before execute this code
        if (!this._isMounted) { return; }

        if (!ReactDOM.findDOMNode(this).contains(event.target) && (typeof event.target.className == "string" && event.target.className.indexOf("do-not-close") == -1)) {

            this.timeout = 475;
            this.collapse();
        } else {

            this.timeout = 0;
        }
    }


    onMouseDownHandler(e) {
        this.mouseX = e.clientX;
        this.mouseY = e.clientY;
    }
    onMouseUpHandler(e) {
        if (Math.abs(this.mouseX - e.clientX) > 10 || Math.abs(this.mouseY - e.clientY) > 5) {
            this.isDragging = true;
        } else {
            this.isDragging = false;
        }
    }
    onClickHandler(e) {
        if (this.isDragging) return;
        if (e.target.classList.contains("dnn-prompt-cmd-insert")) {
            // insert command and set focus
            //this.inputEl.value = e.target.dataset.cmd.replace(/'/g, '"');
            //this.inputEl.focus();
        } else {
            this.focus();
        }
    }

    onKeyDown(e) {
        const self = this;
        // CTRL + `
        if (e.ctrlKey && e.keyCode === 192) {
            if (self.wrapper[0].offsetLeft <= 0) {
                // self.util.loadPanel("Dnn.Prompt", {
                //     moduleName: "Dnn.Prompt",
                //     folderName: "",
                //     identifier: "Dnn.Prompt",
                //     path: "Prompt"
                // });
            } else {
                self.util.closePersonaBar();
            }
            return;
        }

        if (self.isBusy) return;

        // All other keys, only trap if focus is in console.
        // if (self.inputEl === document.activeElement) {
        //     switch (e.keyCode) {
        //         case 13: // enter key
        //             return self.runCmd();
        //         case 38: // Up arrow
        //             if ((self.history.length + self.cmdOffset > 0)) {
        //                 self.cmdOffset--;
        //                 self.inputEl.value = self.history[self.history.length + self.cmdOffset];
        //                 e.preventDefault();
        //             }
        //             break;
        //         case 40: // Down arrow
        //             if ((self.cmdOffset < -1)) {
        //                 self.cmdOffset++;
        //                 self.inputEl.value = self.history[self.history.length + self.cmdOffset];
        //                 e.preventDefault();
        //             }
        //             break;
        //     }
        // }
    }

    render() {
        return (
            <div className="dnn-prompt workspace" style={{ display: "block" }}>
                <Output className="Output"></Output>
                <br />
                <Input className="Input"></Input>
            </div >
        );
    }
}
App.PropTypes = {
    dispatch: PropTypes.func.isRequired
};

function mapStateToProps(state) {
    return {};
}

export default connect(mapStateToProps)(App);


