import { prompt as ActionTypes } from "../constants/actionTypes";
export default function logList(state = {
    commandList: [],
    pagingInfo: {},
    reload: false,
    fieldOrder: [],
    output: "",
    isHtml: false,
    isError: false,
    data: []
}, action) {
    switch (action.type) {
        case ActionTypes.RETRIEVED_COMMAND_LIST:
            return {
                ...state,
                commandList: action.data.commands
            };
        case ActionTypes.EXECUTED_COMMAND:
            return {
                ...state,
                pagingInfo: action.data.pagingInfo,
                isHtml: action.data.isHtml,
                isError: action.data.isError,
                reload: action.data.mustReload,
                data: action.data.data,
                fieldOrder: action.data.fieldOrder,
                output: action.data.output
            };
        default:
            return {
                ...state
            };
    }
}



